import Time from 'common/time.js'
const mutations={
	getSession(state,data){
		state.sessionKey=data.sessionKey
	},
	getstuNum(state,num){
		state.userInfo.stuNum=num;
		console.log(state.userInfo.stuNum)
	},
	getonLogin(state,obj){//登录
		let userName=obj.userName;
		let userHead=obj.userHead;
		let user={
			userHead:userHead,
			userName:userName,
		}
		state.userInfo=user;
	},
	getMsgType(state,{type}){
		state.msgType=type
	},
	getCardType(state,type){
		state.cardType=type
	},
	getGoodsAll(state,goods){
		state.goodsList=goods;
	},
	getMessage(state,data){
		console.log(data)
	}
}

export default mutations